// Misc.
  $misc: (
    z-index-base:    10000
  );

// Duration.
  $duration: (
    menu:        0.35s,
    transition:      0.2s,
    banner:        2.5s
  );

// Size.
  $size: (
    border-radius:    4px,
    element-height:    2.75em,
    element-margin:    2em,
    inner:        65em
  );

// Font.
  $font: (
    family:        ('Source Sans Pro', Helvetica, sans-serif),
    family-fixed:    ('Courier New', monospace),
    weight:        300,
    weight-bold:    600,
    letter-spacing:    0.025em,
    letter-spacing-alt:  0.1em
  );

// Palette.
  $palette: (
    bg:          #242943,
    bg-alt:        #2a2f4a,
    bg-read:  #f2f2f2,
    fg:          #ffffff,
    fg-read:  #373D3f,
    fg-bold:      #ffffff,
    fg-light:      #f1f1f1,
    fg-light-alt:      rgba(244,244,255,0.2),
    border:        rgba(212,212,255,0.1),
    border-bg:      rgba(212,212,255,0.035),
    highlight:      #9bf1ff,
    highlight-read:  #107896,
    accent1:      #6fc3df,
    accent2:      #8d82c4,
    accent3:      #ec8d81,
    accent4:      #e7b788,
    accent5:      #8ea9e8,
    accent6:      #87c5a4
  );
